@use 'sass:math';

.service-page {
    line-height: math.div(26px,16px) * 100%;
    section {
        margin-bottom: 56px;
        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }
    }
    // .service-page__title
    &__title {
        margin-bottom: 56px;
        @media (max-width:$md4+px){
             margin-bottom: 24px; 
        }
    }
    // .service-page__technical
    &__technical {
    }
    // .service-page__complex
    &__complex {
    }
    // .service-page__brakes
    &__brakes {
    }
    // .service-page__preference
    &__preference {
    }
}
.technical {
    // .technical__container
    &__container {
    }
    // .technical__title
    &__title {
        margin: 0px 0px math.div(24px,28px) * 1em 0px; 

        font-size: 28px;
        font-weight: 700;
        line-height: math.div(36px,28px) * 100%;
        letter-spacing: math.div(.3px,28px) * 1em;

        @media (max-width:$md4+px){
            max-width: 232px;
            font-size: 16px; 
        }

        &_attention {
            font-size: 22px;
            letter-spacing: math.div(.3px,22px) * 1em;

            @media (max-width:$md4+px){
                 font-size: 14px; 
            }
        }
    }
    // .technical__content
    &__content {
        max-width: 745px;
    }
    // .technical__text
    &__text {
        margin-bottom: 16px;

        @media (max-width:$md4+px){
             margin-bottom: 8px;
        }

        p {
            &:not(:last-child){
                margin:0px 0px 16px 0px;

                @media (max-width:$md4+px){
                     margin: 0px 0px 8px 0px;  
                }
            }
         }

        // .technical__text_attention
        &_attention {
            font-size: 13px;

            @media (max-width:$md4+px){
                 font-size: 10px; 
            }
        }
    }
    // .technical__link
    &__link {
        color: var(--main-yellow);
        text-decoration: underline;

        transition: color 0.3s ease 0s;

        @media (any-hover: hover){
            &:hover{
                color: var(--hover-color);
            }
        }

        @media (max-width:$md4+px){
             display: block;
             font-size: 13px; 
             font-weight: 800;
        }
    }
}
.complex {
    // .complex__container
    &__container {
    }
    // .complex__content
    &__content {
        max-width: 745px;

        &_attention {
            font-size: 13px;

            @media (max-width:$md4+px){
                 font-size: 10px; 
            }
        }
    }
    // .complex__label
    &__label {
    }
    // .complex__list
    &__list {
        margin-bottom: 16px;
        padding-left: 25px;

        @media (max-width:$md4+px){
             margin-bottom: 8px;
        }
        li {
            list-style: disc;
        }
    }
    // .complex__price
    &__price {
    }
}
.price-complex {
    // .price-complex__title
    &__title {
        margin-bottom: 16px;
    }
    // .price-complex__body
    &__body {
    }
    // .price-complex__row
    &__row {
        display: flex;
        padding: 8px 0;

        border-top: 1px solid var(--main-gray);

        @media (max-width:$md4+px){
             flex-direction: column; 

             border: none;
        }

        &:not(:last-child){
            margin:0px 0px 32px 0px;

            @media (max-width:$md4+px){
                 margin-bottom: 24px; 
            }
        }
    }
    // .price-complex__label
    &__label {
        flex: 0 1 math.div(320,745) * 100%;
        font-weight: 600;

        @media (max-width:$md4+px){
            flex: 0 1 auto;
            align-self: center;
            width: 100%;
            margin-bottom: 8px; 
            padding-bottom: 8px;

            text-align: center;

            border-bottom: 1px solid var(--light-gray);
        }

        h4 {
            max-width: 234px;
            @media (max-width:$md4+px){
                 max-width: 100%; 
            }
        }
    }
    // .price-complex__content
    &__content {
        flex: 0 1 math.div(425,745) * 100%;

        display: flex;

        @media (max-width:$md4+px){
             flex: 0 1 auto; 

             justify-content:space-between;
        }
    }
    // .price-complex__name
    &__name {
        flex: 0 1 math.div(234,425) * 100%;
        font-size: 13px;
        color: var(--add-gray);

        @media (max-width:$md4+px){
             flex: 0 1 125px; 
        }
    }
    // .price-complex__price
    &__price {
        flex: 0 1 math.div(192,425) * 100%;
        display: flex;
        justify-content: center;

        font-size: 13px;

        @media (max-width:$md4+px){
             flex: 0 1 auto;
             
             justify-content: flex-end;
        }
    }
}
.brakes {
    // .brakes__container
    &__container {
    }
    // .brakes__title
    &__title {
    }
    // .brakes__content
    &__content {
        max-width: 745px;
    }
}
.preference {
    // .preference__container
    &__container {
    }
    // .preference__title
    &__title {
    }
    // .preference__content
    &__content {
        max-width: 745px;
    }
}
