@use 'sass:math';


.hero-product-page {

    // .hero-product-page__container
    &__container {}

    // .hero-product-page__title
    &__title {
        margin-bottom: 19px;

        font-size: 44px;
        @include adaptiv-font(44, 22, 1);
        font-weight: 700;
        letter-spacing: math.div(.3px, 44px) * 1em;
        line-height: 100%;
        text-align: center;

        @media (max-width:$md4+px) {
            margin-bottom: 14px;

            line-height: math.div(28px, 22px) * 100%;
        }

        &_mob {
            display: none;
            @media (max-width:$md4+px){
                 display: block; 
            }
        }
    }

    // .hero-product-page__info
    &__info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 44px;

        @media (max-width:$md4+px){
             margin-bottom: 25px; 
        }

        &_mob {
            display: none;
            @media (max-width:$md4+px){
                 display: flex; 
            }
        }
    }

    // .hero-product-page__estimation
    &__estimation {
        flex: 0 1 math.div(761,1342) * 100%;

        display: flex;
        justify-content: flex-end;

        @media (max-width:$md3+px){
             flex: 0 1 auto;
             justify-content: flex-start; 
        }
    }

    // .hero-product-page__articol
    &__articol {
        color: rgba(17, 17, 17, 0.45);
        font-size: 13px;
        font-weight: 800;
        text-transform: uppercase;

        @media (max-width:$md4+px){
             font-size: 10px;
             line-height: math.div(16px,10px) * 100%;
        }
    }

    // .hero-product-page__content
    &__content {
        position: relative;

        @media (max-width:$md3+px){
             margin-bottom: 30px; 
        }
    }

    // .hero-product-page__slider
    &__slider {
        margin-bottom: 34px;

        @media (max-width:$md4+px){
             margin-bottom: 24px; 
        }
    }

    // .hero-product-page__slide
    &__slide {
        display: flex;
        justify-content:center;
    }
    &__image {
       flex: 0 1 math.div(890,1342) * 100%;
        padding: 0px 0px 40.1% 0px;

        @media (max-width:$md4+px){
             flex: 1 1 100%; 
             padding: 0px 0px 60% 0px;
        }
    }
    // .hero-product-page__mob-buttons
    &__mob-buttons {
        display: none;

        justify-content:center;
        align-items: center;

        -webkit-tap-highlight-color: rgba(0,0,0,0);

        @media (max-width:$md3+px){
             display: flex; 
        }

    }
    // .hero-product-page__button
    &__button {
        display: flex;
        justify-content:center;
        align-items: center;
        max-width: 24px;

        background-color: transparent;
        &:not(:last-child){
            margin:0px 30px 0px 0px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        ._favourites {
            width: 24px;
            height: 24px;
        }

        svg {
            width: 24px;
            height: 24px;
        }

        ._favourites-inactive {
            path {
                fill: var(--dark-gray);
            }
        }
    }

}