@use 'sass:math';

.mini-cart {
    position: relative;
    width: 320px;
    max-height: 425px;

    padding: 28px 0 0;


    background-color: var(--main-white);

    // .mini-cart__quantity
    &__quantity {
        position: absolute;
        top: -2px;
        right: -6px;

        width: 16px;
        height: 16px;


        display: flex;
        justify-content:center;
        align-items: center;
        border-radius: 40px;

        font-weight: 600;
        font-size: 10px;
        line-height: 94%;

        background-color: var(--main-yellow);
    }


    // .mini-cart__label
    &__label {
        @media (min-width:$md2+px){
            margin-bottom: 12px;
            padding: 0px 22px 0px 16px;
        
            font-size: 19px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: math.div(.3px,19px) * 1em;
            line-height: math.div(26px,19px) * 100%; 
        }
    }
    // .mini-cart__items
    &__items {
        display: none;
        padding: 0px 22px 15px 16px;
        max-height: 360px;
        overflow: auto;

        &._active{
            display: block;

            & + .mini-cart__bottom {
                display: flex;
            }
        }
    }
    // .mini-cart__item
    &__item {
        display: flex;
        align-items:center;

        &:not(:last-child){
            margin:0px 0px 32px 0px;
        }
    }
    // .mini-cart__image
    &__image {
        flex: 0 1 102px;
        margin-right: 22px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    // .mini-cart__info
    &__info {
    }
    // .mini-cart__product-name
    &__product-name {
        font-size: 13px;
        font-weight: 600;
        line-height: math.div(21px,13px) * 100%;
    }
    // .mini-cart__prices
    &__prices {
        display: flex;
        justify-content:space-between;
        align-items:center;
    }
    // .mini-cart__new-price
    &__new-price {
        flex: 1 1 auto;
        margin-right: 15px;

        font-weight: 800;
        letter-spacing: math.div(.3px,16px) * 1em;
        line-height: math.div(26px,16px) * 100%;
    }
    // .mini-cart__old-price
    &__old-price {
        color: var(--medium-gray);
        font-size: 13px;
        font-weight: 600;
        line-height: math.div(21px,13px) * 100%;
        text-decoration: line-through;
    }
    // .mini-cart__bottom
    &__bottom {
        position: sticky;
        bottom: 0px;
        left: 0;
        display: none;
        align-items:center;
        width: 100%;
        height: 61px;
    }
    // .mini-cart__total
    &__total {
        flex: 0 1 50%;

        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 8px 16px;

        color: var(--main-white);
        background-color: var(--darkest-gray);
    }
    // .mini-cart__label-total
    &__label-total {
        font-size: 13px;
        font-weight: 500;
        line-height: math.div(21px,13px) * 100%;
    }
    // .mini-cart__total-sum
    &__total-sum {
        font-size: 19px;
        font-weight: 700;
        letter-spacing: math.div(.3px,19px) * 1em;
        line-height: math.div(24px,19px) * 100%;
    }
    // .mini-cart__submit
    &__submit {
        flex: 1 1 50%;
        height: 100%;

        font-size: 16px;
        line-height: math.div(26px,16px) * 100%;
        font-weight: 800;
    }
    &__empty {
        padding: 0 16px 19px;
        font-size: 13px;
        line-height: 21px;
        display: none;
        &._active{
            display: block;
        }
    }
}
