@use 'sass:math';


.product-reviews {

    // .product-reviews__container
    &__container {}

    // .product-reviews__title
    &__title {
        @include adaptiv-value("margin-bottom", 110, 40, 1);

        @media (max-width:$md4+px){
             display: none; 
        }
    }

    // .product-reviews__content
    &__content {
        display: flex;
        justify-content: space-between;

        @media (max-width:$md2+px){
            flex-direction: column;
        }
    }

    // .product-reviews__body
    &__body {
        flex: 0 1 math.div(960, 1344) * 100%;

        @media (max-width:$md4+px){
             margin-bottom: 16px; 
        }
    }

    // .product-reviews__sort
    &__sort {
        display: flex;
        align-items: center;
        margin-bottom: 32px;

        @media (max-width:$md4+px){
             margin-bottom: 24px; 
        }
    }

    // .product-reviews__label
    &__label {
        margin-right: 16px;

        @media (max-width:$md4+px){
            margin-right: 12px;

            font-size: 13px; 
        }
    }

    // .product-reviews__form
    &__form {}

    // .product-reviews__item
    &__item {
        display: flex;

        @media (max-width:$md2+px){
            flex-direction: column;
            justify-content:center;
            align-items: center;
        }

        &:not(:last-child){
            margin:0px 0px 64px 0px;

            @media (max-width:$md4+px){
                margin:0px 0px 32px 0px;
            }
        }
    }

    // .product-reviews__autor
    &__autor {
        flex: 0 1 math.div(256, 960) * 100%;
        padding-top: 8px;

        @media (max-width:$md2+px){
            padding-top: 0;
            margin-bottom: 16px;
            flex: 0 1 auto;
        }
    }

    // .product-reviews__rating
    &__rating {}

    // .product-reviews__description
    &__description {
        flex: 0 1 math.div(704, 960) * 100%;
        padding: 24px 32px 38px;

        background-color: var(--dark-white);
        border-radius: 4px;

        @media (max-width:$md2+px){
            flex: 0 1 auto;
        }
        @media (max-width:$md4+px){
             padding: 16px 13px 16px 8px; 
        }
    }

    //.product-reviews__row
    &__row {
        position: relative;
        padding-left: 38px;

        line-height: math.div(26px,16px) * 100%;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 24px;
            height: 24px;

            @media (max-width:$md4+px){
                 width: 22px;
                 height: 22px; 
            }

        }

        &:not(:last-child) {
            margin: 0px 0px 35px 0px;

            @media (max-width:$md4+px){
                margin: 0px 0px 16px 0px;
            }
        }

        //.product-reviews__row_plus
        &_plus {
            &::before {
                background: url('../img/icons/plus.svg') 0 0 no-repeat;
            }
        }

        //.product-reviews__row_minus
        &_minus {
            &::before {
                background: url('../img/icons/minus.svg') 0 0 no-repeat;
            }
        }

        //.product-reviews__row_message
        &_message {
            &::before {
                background: url('../img/icons/message.svg') 0 0 no-repeat;
            }
        }
    }

    // .product-reviews__info
    &__info {
        flex: 0 0 224px;
        @media (max-width:$md2+px){
            flex: 0 1 auto;
            display: flex;
            flex-direction: column;
            justify-content:center;
        }
    }

    // .product-reviews__checkbox
    &__checkbox {
        position: relative;
        cursor: pointer;

        &::before {
            content: '';
            position: absolute;
            left: -34px;
            top: -7px;
            width: 16px;
            height: 16px;
            background-color: #fff;
            border: 1px solid #ccc;
            border-radius: 2px;

            transition: background-color 0.3s ease 0s;
        }

        &::after {
            content: '';
            position: absolute;
            left: -28.5px;
            top: -4.2px;
            width: 5px;
            height: 8px;
            border: solid white;
            border-width: 0 2px 2px 0;
            -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }

    // .product-reviews__input
    &__input {
        position: absolute;
        width: 0;
        height: 0;
        opacity: 0;
        visibility: hidden;

        &:checked~.product-reviews__checkbox {
            &::before {
                background-color: var(--main-yellow);
            }
        }
    }
}

.autor-review {
    display: flex;
    flex-direction: column;

    @media (max-width:$md2+px){
        flex-direction: row;
        justify-content:space-between;
        width: 100%;
    }

     // .autor-review__info
     &__info {
        margin-bottom: 20px;

        @media (max-width:$md2+px){
            margin-bottom: 0;
        }
     }

      // .autor-review__stars
    &__stars{
        @media (max-width:$md4+px){
             display: flex;
             flex-direction: column;
             align-items: flex-end; 
        }
    }

    // .autor-review__name
    &__name {
        margin-bottom: 4px;

        line-height: math.div(26px, 16px) * 100%;
    }

    // .autor-review__date
    &__date {
        color: rgba(17, 17, 17, 0.75);
        font-size: 13px;
        line-height: math.div(21px, 13px) * 100%;
    }

    // .autor-review__recommend
    &__recommend {
        display: flex;
        align-items: center;
        margin-bottom: 5px;

        color: var(--ligth-green);
        font-weight: 600;
        line-height: 26px;

        @media (max-width:$md4+px){
             font-size: 13px; 
        }

        svg {
            margin-right: 6px;
        }
    }
}

.info-reviews {

    // .info-reviews__label
    &__label {
        margin-bottom: 41px;

        font-size: 19px;
        font-weight: 700;
        letter-spacing: math.div(.3px, 19px) * 1em;
        line-height: math.div(24px, 19px) * 100%;
        @media (max-width:$md2+px){
            display: none;
        }
    }
    // .info-reviews__rows
    &__rows {
        margin-bottom: 35px;

        @media (max-width:$md2+px){
            display: none;
        }
    }
    // .info-reviews__row
    &__row {
        display: flex;
        align-items: center;
        padding-left: 34px;

        &:not(:last-child) {
            margin: 0px 0px 8px 0px;
        }
    }

    // .info-reviews__stars
    &__stars {
        position: relative;
        margin-right: 17px;

        &::before {
            content: "";
            display: block;
            color: var(--main-yellow);

            @media (max-width:$md4+px) {
                content: "";
            }
        }
    }

    // .info-reviews__active-stars
    &__active-stars {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        overflow: hidden;

        &::before {
            content: '';
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            color: var(--main-yellow);
        }

        &_2 {
            width: 80%;
        }

        &_3 {
            width: 60%;
        }

        &_4 {
            width: 40%;
        }

        &_5 {
            width: 20%;
        }
    }

    // .info-reviews__quantity
    &__quantity {}

    // .info-reviews__button
    &__button {
        display: flex;
        justify-content:center;
        align-items: flex-start;
        padding: 14px 38px;

        color: var(--main-yellow);
        font-size: 13px;
        font-weight: 800;

        border: 2px solid var(--main-yellow);
        background-color: var(--main-white);

        @media (max-width:$md2+px){
            max-width: 288px;
            margin: 0px auto;
        }

        svg {
            margin-right: 14px;
        }

        span {
            line-height: math.div(21px,13px) * 100%;
        }
    }
     // .info-reviews__show-all
     &__show-all {
        display: none;
        @media (max-width:$md4+px){
            display: flex;
            justify-content:center;
            align-items: flex-start;
            margin-bottom: 32px;
    
            color: var(--main-yellow);
            font-size: 13px;
            font-weight: 800;
    
            background-color: var(--main-white); 
        }
    }
}