@use 'sass:math';

//====================================================================================================
.header {
	width: 100%;
	background-color: var(--main-white);
	z-index: 10;
	position: fixed;
	top: 0;

	// .header__sidebar
	&__sidebar {
		position: absolute;
		top: 89px;
		left: 0;
		width: 100%;
		margin: 0 auto;
		z-index: 10;

		// background-color: var(--light-gray);

		transition: top 0.3s ease 0s;
	}

	& .header__top {
		transition: all 0.3s ease 0s;
	}

	&._scroll {
		padding-bottom: 20px;
		padding: 10px 0px 20px;
		animation: showHeader 1s ease-out forwards;

		@media (max-width: 1387px){
			.user-bar__item_search {
				opacity: 0;
				padding: 0;
				// width: 0;
				height: 0;
				visibility: hidden;
				margin-bottom: 0;
			}
		}

		& .header__top {
			opacity: 0;
			padding: 0;
			// width: 0;
			height: 0;
			visibility: hidden;
		}

		& .header__sidebar {
			top: 75px;
		}

		@media (max-width: 620px) {
			padding-bottom: 0;
		}
	}

	// .header__content
	&__content {}

	// .header__top
	&__top {
		padding: 12px 0 7px;
	}

	// .header__main
	&__main {}
}

.top-header {
	display: flex;
	justify-content: space-between;
	align-items: center;

	font-size: 13px;
	color: #797979;
	line-height: math.div(21px, 13px) * 100%;

	border-bottom: 1px solid rgba(46, 54, 70, 0.2);

	@media (max-width:$md2+px) {
		position: relative;
		z-index: 3;
	}

	@media (max-width: 620px) {
		display: none;
	}

	// .top-header__info
	&__info {
		flex: 0 1 var(--container);

		display: flex;
		align-items: center;

		@media (max-width: 620px) {
			flex: 0 1 auto;

			justify-content: space-between;
		}
	}

	// .top-header__town
	&__town {
		display: flex;
		align-items: center;

		margin-right: 25px;

	}

	// .top-header__geo
	&__geo {
		margin-right: 6px;
	}

	// .top-header__text
	&__text {}

	// .top-header__delivery
	&__delivery {
		margin-right: 25px;
	}

	// .top-header__credit
	&__credit {}

	// .top-header__phone
	&__phone {
		display: flex;
		align-items: center;

		@media (max-width: 620px) {
			position: relative;
			z-index: 3;
		}
	}

	// .top-header__icon
	&__icon {
		margin-right: 8px;

		@media (max-width: 620px) {
			margin-right: 0;
		}

		svg {
			width: 11px;
			height: 11px;

			@media (max-width: 620px) {
				width: 18px;
				height: 18px;
			}
		}
	}

	// .top-header__number
	&__number {
		line-height: 78%;

		@media (max-width: 620px) {
			display: none;
		}
	}
}

.main-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 11px;

	@media (max-width:$md2+px) {
		position: relative;
		flex-wrap: wrap;
	}

	@media (max-width:$md4+px) {
		padding-top: 10px;
	}

	// .main-header__logo
	&__logo {

		flex: 0 0 128px;
		margin-right: 64px;
		padding-bottom: 5px;

		@media (max-width:$md1+px) {
			margin-right: 32px;
		}

		@media (max-width:$md2+px) {
			position: absolute;
			top: 50%;
			top: 28px;
			left: 50%;

			width: 128px;
			transform: translate(-50%, -50%);
			z-index: 3;
			width: 128px;
			margin-right: 0;
		}

		@media (max-width: 620px) {
			position: relative;
			z-index: 3;
			top: auto;
			left: auto;
			transform: translate(0px, 0px);
			flex: 0 1 auto;
			padding-bottom: 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 7px;
			transition: all 0.3s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover {
				img {
					transform: translate(0px, -2px);
					box-shadow: 2px 3px 3px 0px rgba(46, 54, 70, 0.2);
				}
			}
		}
	}

	// .main-header__menu
	&__menu {
		flex: 1 1 auto;

		&.hidden {
			@media (min-width:$md2+px){
				display: none; 
			}
		}

		@media (max-width: 620px) {
			flex: 0 1 auto;
			order: -1;
		}
	}

	// .main-header__user-bar
	&__user-bar {
		flex: 0 0 190px;
	}
}

.menu {
	font-size: 13px;
	font-weight: 600;
	line-height: math.div(21px, 13px) * 100%;


// display: none;
	// .menu__icon
	&__icon {}

	// .menu__body
	&__body {
		@media (max-width: $md2+px) {
			opacity: 0;
			position: fixed;
			top: 0;
			left: 0;

			height: 100%;
			width: 100%;
			padding: 120px 15px 40px;

			visibility: hidden;
			overflow: auto;
			z-index: 2;

			transition: all .3s ease 0s;

			background-color: var(--main-white);

			&::before {
				content: "";
				position: fixed;
				width: 100%;
				height: 77px; //      
				top: 0;
				left: 0;
				z-index: 3;
				background-color: var(--main-white);

				@media (max-width:620px) {
					height: 65px;
				}
			}

			&._active {
				opacity: 1;
				visibility: visible;
			}
		}
		@media (max-width:$md4+px){
			 padding-top: 90px; 
		}

	}

	// .menu__list
	&__list {
		max-width: 555px;
		display: flex;
		justify-content: space-between;
		align-items: center;

		@media (max-width: $md2+px) {
			max-width: 750px;
			width: 100%;
			margin: 0 auto;
			padding-left: 15px;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			margin-bottom: 40px;

			font-weight: 600;
		}
		@media (max-width:$md3+px){
			 padding-left: 0; 
		}
	}

	// .menu__link
	&__link {
		position: relative;

		@media (max-width: $md2+px){
			width: 100%;
			  display: flex;
			  justify-content:space-between;
			  align-items:center;
		}

		&::after{
            @media (max-width: $md2+px){
                content:'';
                display: block;
                width: 16px;
                height: 16px;
                background: url('../img/icons/arrow-partners.svg') 0 0 no-repeat;
            }
        }



		&._active {
			&>a {
				color: var(--hover-color);
			}

		}

		&>a {
			text-transform: uppercase;
			color: var(--main-black);

			&:active {
				color: var(--add-gray);
			}

			@media (max-width:$md4+px){
				 text-transform: none; 
			}
		}

		&:not(:last-child) {
			margin: 0px 32px 0px 0px;

			@media (max-width: $md2+px) {
				margin: 0px 0px 14px 0px;
			}
		}

		@media (any-hover: hover) {
			a {
				transition: color 0.3s ease 0s;

				&:hover {
					color: var(--hover-color);
				}
			}

		}
	}

	//menu__sublist {
	&__sublist {
		position: absolute;
		top: 27px;
		left: -95px;
		width: 757px;
		height: 704px;
		flex-direction: column;
		flex-wrap: wrap;
		padding: 29px 0px 0px 96px;

		background-color: var(--main-white);

		display: none;


		&._active {
			display: flex;
		}
	}
}

.user-bar {
	position: relative;
	display: flex;
	align-items: center;
	

	flex: 1 1 auto;
	justify-content: flex-end;
	height: 100%;

	@media (max-width:$md2+px) {
		position: relative;
		z-index: 3;
	}

	@media (max-width:620px) {
		display: none;
	}

	& > div {
		cursor: pointer;
	}
	//.user-bar__hide-search
	&__hide-search {
		display: none;
		color: var(--main-yellow);
		font-weight: 800;
		letter-spacing: 0.3px;
		flex: 0 1 160px;

		cursor: pointer;

		transition: color 0.3s ease 0s;

		@media (any-hover: hover){
			&:hover{
				color: var(--hover-color);
			}
		}
	}
	//.user-bar__clear-input-btn
	&__clear-input-btn {
		display: none;
		z-index: 10;

		width: 30px;
		height: 30px;

		position: absolute;
		right: 170px;
		top: 50%;

		transform: translate(0px,-50%);

		cursor: pointer;

		background: url(../img/icons/sprites/close.svg) center / 15px 15px no-repeat;


		@media (max-width:$md2+px){
			right: 10px;
		}
	}

	// .user-bar__item
	&__item {
		&:not(:last-child) {
			margin: 0px 35px 0px 0px;
	}

		// .user-bar__item_search
		&_search {
			flex: 1 1 auto;

			@media (max-width: $md2+px) {
				flex: 1 1 100%;
				margin: 16px 0px 20px;

				transition: opacity 0.3s ease 0s,
					visibility 0.3s ease 0s;
			}

			svg {
				width: 24px;
				height: 24px;

				@media (max-width: 620px) {
					width: 18px;
					height: 18px;
				}
			}

			&._active{
				position: relative;
				display: flex;
				justify-content:space-between;
				align-items: center;
				width: 90%;

				.user-bar__clear-input-btn {
					display: block;
				}

				.user-bar__label {
					justify-content: flex-start;
					width: 79%; 

					@media (max-width:$md2+px){
						width: 100%;
					}
				}
				.user-bar__hide-search {
					@media (min-width:$md2+px){
						display: inline-flex; 
					}
				}
			}
		}
	}
	// .user-bar__compare
	&_compare {}

	// .user-bar__favourites
	&_favourites {}

	// .user-bar__cart
	&_cart {
		position: relative;
	}

	//.user-bar__label
	&__label {
		position: relative;

		display: flex;
		align-items:center;
		justify-content: flex-end;

		@media (max-width:$md4+px){
			-webkit-tap-highlight-color: transparent; 
		}
	}

	//.user-bar__input
	&__input {
		width: 0;
		height: 40px;
		padding: 0;

		opacity: 0;

		color:  var(--add-gray);
		font-size: 16px;
		line-height: 26px;

		@media (min-width:$md1+px){
			transition: width 0.3s ease 0s,
			padding 0.3s ease 0s; 
		}

		@media (max-width:$md2+px){
			 opacity: 1;
			 width: 100%; 
			 padding: 10px 45px;
			 border: 1px solid var(--main-gray);
		}
		


		&._active{

			@media (min-width: $md2+px){
				width: 100%;
				padding: 10px 30px;
				opacity: 1;
				
	
				& + .user-bar__icon{
					left: -10px;
					width: 24px;
    				height: 24px;
				}
			}
		}
	}
	//user-bar__search-list 
	&__search-list {
		position: absolute;
		bottom: -166px;
		left: -6px;	
		width: 79%;
		display: none;
		padding-left: 40px;

		background-color: var(--main-white);

		@media (max-width:$md2+px){
			bottom: -169px;
		}
		@media (max-width:$md4+px){
			 left: 5px; 
			 bottom: -233px;
		}

		li {
			padding: 13px 0px;
		}
		&._show {
			display: block;
		}
	}

	//.user-bar__icon
	&__icon {
		margin-left: 10px;
		cursor: pointer;

		position: absolute;
		top: 50%;
		right: -3px;

		transform: translate(0px,-50%);

		transition: right 0.3s ease 0s;

		@media (max-width: $md2+px){
			  left: 5px;
		}
	}

	// .user-bar__popups
	&__popups {

	}

	// .user-bar__popup
	&__popup {
		visibility: hidden;
		opacity: 0;
		pointer-events: none;

		position: absolute;
		top: 31px;
		right: 0;
		z-index: 10;

		cursor: auto;

		transition: visibility 0.3s ease 0s,
					opacity 0.3s ease 0s;

		&._active{
			opacity: 1;
			visibility: visible;
			pointer-events: auto;
		}

		// .user-bar__popup_mini-cart
		&_mini-cart {
			
		}
	}

}

//====================================================================================================

//Burger
.icon-menu {
	display: none;

	@media (max-width: $md2+px) {
		display: block;
		position: absolute;
		top: calc(50% + 5px);
		top: 25px;
		left: 0px;
		width: 30px;
		height: 18px;
		cursor: pointer;
		z-index: 5;

		transform: translate(0px, -50%);

		@media (max-width: 620px) {
			position: relative;
			top: 0;
			left: 0;
			transform: translate(0px, 0px);
			order: -1;
		}

		@media (max-width:$md4+px) {
			width: 18px;
			height: 12px;
		}

		span {
			transition: all 0.3s ease 0s;
			top: calc(50% - 1px);
			left: 0px;
			position: absolute;
			width: 100%;
			height: 2px;
			background-color: var(--dark-gray);

			&:first-child {
				top: 0px;
			}

			&:last-child {
				top: auto;
				bottom: 0px;
			}
		}

		&._active {
			span {
				transform: scale(0);

				&:first-child {
					transform: rotate(-45deg);
					top: calc(50% - 1px);
				}

				&:last-child {
					transform: rotate(45deg);
					bottom: calc(50% - 1px);
				}
			}
		}
	}
}


@keyframes showHeader {
	0% {
		position: fixed;
		top: 0;
		margin-top: -150px;
	}

	100% {
		position: fixed;
		top: 0;
		margin-top: 0;

	}
}