@use 'sass:math';


.benefits {
    background-color: var(--darkest-gray);

    // .benefits__container
    &__container {}

    // .benefits__content
    &__content {
        padding: 96px 20px;

        @media (max-width:$md4+px) {
            padding: 48px 20px;
        }

    }
    // .benefits__title
    &__title {
        padding-top: math.div(45px,34px) * 1em;

        color: var(--main-white);

        &_product-page {
            @media (max-width:$md4+px){
                 display: none; 
            }
        }
    }

    // .benefits__items
    &__items {
        display: flex;
        justify-content: center;

        @media (max-width:$md1+px) {
            flex-wrap: wrap;
        }

        @media (max-width:$md2+px) {
            flex-direction: column;
        }
        @media (max-width:$md4+px){
             max-width: 224px;
             margin: 0px auto; 
        }
    }

    // .benefits__item
    &__item {
        position: relative;
        flex: 0 1 24.5%;

        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 48px 31px 32px;

        background-color: var(--darken-gray);
        border-radius: 8px;

        @media (max-width:$md1+px) {
            flex: 0 1 40%;
        }

        @media (max-width:$md4+px) {
            padding: 36px 16px 16px;
        }

        &::after {
            content: '';
            position: absolute;
            top: -32px;
            left: 50%;
            transform: translate(-50%, 0px);

            width: 64px;
            height: 64px;

            border-radius: 50%;
            background: linear-gradient(90deg, #D77009 0%, #FFAC00 100%);

            @media (max-width:$md4+px) {
                width: 48px;
                height: 48px;
                top: -24px;
            }
        }

        &::before {
            content: '';
            width: 48px;
            height: 48px;
            z-index: 2;
            position: absolute;
            left: 50%;
            top: -25px;
            display: block;

            background-position: -8px 0;
            background-size: cover;
            background-repeat: no-repeat;

            transform: translate(-50%, 0px);

            @media (max-width:$md4+px){
                 width: 30px;
                 height: 30px; 
                 top: -15px;

                 background-position: -5px 0;
            }
        }

        &:not(:last-child) {
            margin: 0px 64px 0px 0px;

            @media (max-width:$md1+px) {
                margin: 0px 0px 65px 0px;
            }
            @media (max-width:$md4+px){
                  margin-bottom: 44px;
            }
        }

        &_first.benefits__item {

            @media (max-width:$md1+px) {
                margin-right: 64px;
            }

            @media (max-width:$md2+px) {
                margin-right: 0;
            }

            &::before {
                background-image: url('../img/icons/sprites/sprite.svg#account_balance_wallet');
            }
        }

        &_second {
            &::before {
                background-image: url('../img/icons/sprites/sprite.svg#account_balance');
            }
        }

        &_third {
            &::before {
                background-image: url('../img/icons/sprites/sprite.svg#credit_card');
            }
        }


      
    }

    // .benefits__icon
    &__icon {
        position: absolute;
    }

    // .benefits__label
    &__label {
        padding-bottom: 34px;

        font-size: 22px;
        font-weight: 700;
        letter-spacing: math.div(.3px, 22px) * 1em;
        line-height: math.div(28px, 22px) * 100%;

        color: var(--main-white);
        text-align: center;

        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        @media (max-width:$md4+px) {
            padding-bottom: 17px;

            font-size: 16px;
        }
    }

    // .benefits__description
    &__description {
        flex: 1 1 auto;
        margin-bottom: 27px;
        padding-top: 24px;

        color: var(--main-white);
        opacity: 0.75;

        font-size: 16px;
        line-height: math.div(26px, 16px) * 100%;

        text-align: center;

        @media (min-width:$md1+px){
            max-width: 257px; 
        }


        @media (max-width:$md4+px) {
            max-width: 180px;
            margin-bottom: 17px;
            padding-top: 16px;

            font-size: 10px;
        }
    }

    // .benefits__show-more
    &__show-more {

    }
}