
.mob-slider {
    display: none;

    @media (max-width:$md4+px){
         display: block; 
    }
    // .mob-slider__container
    &__container {
    }
    // .mob-slider__content
    &__content {
        display: flex;
        justify-content:space-between;
        align-items: center;
        width: 380px;
        transform: translate(0,0);

        transition: transform 0.3s ease 0s;

        @media (max-width: 405px){
            &._active{
                transform: translate(-17%,0);
            }
        }
    }
    // .mob-slider__slide
    &__slide {
        position: relative;
        padding-bottom: 10px;
        position: relative;
        display: inline-flex;
        font-weight: 600;
        text-transform: uppercase;

        opacity: .6;

        transition: opacity 0.3s ease 0s;

        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%,0px);
            width: 32px;
            height: 4px;

            background: var(--main-yellow);
            border-radius: 2px;

            opacity: 0;

            transition: opacity 0.3s ease 0s;
        }

        &._active{
            opacity: 1; 

            &::after{
                opacity: 1;
            }
        }

        // &:not(:last-child){
        //     margin:0px 20px 0px 0px;
        // }

    }
}
