@use 'sass:math';
//====================================================================================================
input[type="text"],
textarea{

	border: 2px solid var(--light-white);

	transition: border-color 0.3s ease 0s,
				color .3s ease;
	
	&._focus {
		border-color: var(--dark-gray);
		color: var(--main-black);
	}
	&._error {
		border-color: var(--red);

		@media (any-hover: hover){
			&:hover{
				border-color: var(--red);
			}
		}
	}
	@media (any-hover: hover){
		&:hover{
			border-color: var(--add-gray);
			color: var(--main-black);
		}
	}
}

.popup {
	-webkit-overflow-scrolling: touch;
	z-index: 100;
	padding: 64px;
	position: fixed;
	top: 0px;
	left: 0;
	width: 100%;
	height: 100%;
	transition: visibility 0.8s ease 0s;
	visibility: hidden;

	@media (max-width:$md4+px){
		 padding: 57px 0; 
	}
	&::before {
		content: "";
		background: rgba(77, 77, 77, 0.75);
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		opacity: 0;
		transition: opacity 0.8s ease 0s;
	}
	&.show {
		visibility: visible;
		overflow: auto;
		&::before {
			opacity: 1;
		}
		.popup__body {
			transform: scale(1);
		}
	}
	&._active {
		overflow: auto;
		visibility: visible;
		&::before {
			opacity: 1;
		}
		.popup__body {
			transition: all 0.3s ease 0.2s;
			transform: scale(1);
		}
	}
	// .popup__content
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		min-height: 100%;
		flex: 1 1 auto;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	// .popup__body
	&__body {
		transform: scale(0);
		transition: all 0.3s ease 0s;
		background-color: #fff;
		padding: 48px 48px 32px;
		width: 100%;
		max-width: 768px;
		border-radius: 8px;

		@media (max-width:$md4+px){
			 padding: 24px 16px; 
		}
	}
	// .popup__close
	&__close {
		width: 24px;
		height: 24px;
		position: absolute;
		top: 30px;
    	right: 31px;
		cursor: pointer;
		z-index: 30;

		transition: transform .3s ease 0s;

		@media (any-hover: hover){
			&:hover{
				transform: rotate(-90deg);
			}
		}

		@media (max-width:$md4+px){
			top: 12px;
    		right: 8px;
		}

		svg {
			width: 24px;
			height: 24px;

			@media (max-width:$md4+px){
				 width: 14px;
				 height: 14px; 
			}
		}
	}
}
//====================================================================================================
//====================================================================================================

.popup-comment {
	// .popup-comment__title
	&__title {
		margin: 0px 0px math.div(32px,28px) * 1em 0px; 
		font-size: 28px;
		line-height: math.div(36px,28px) * 100%;
		letter-spacing: math.div(.3px,28px) * 1em;
		font-weight: 700;

		@media (max-width:$md4+px){
			 font-size: 19px; 
			 text-align: center;
		}
	}
	// .popup-comment__form
	&__form {
	}
	// .popup-comment__row
	&__row {
		display: flex;
		flex-direction: column;
		margin-bottom: 24px;

		-webkit-tap-highlight-color: rgba(0,0,0,0);
	}
	// .popup-comment__subtitle
	&__subtitle {
		display: block;
		margin-bottom: 6px;

		font-size: 13px;
		line-height: math.div(21px,13px) * 100%;
		color: var(--dark-gray);

		span {
			margin-right: 3px;

			font-size: 16px;
			color: var(--red);
		}

	}
	// .popup-comment__input
	&__input {
		width: 256px;
		padding: 13px 16px;

		color: var(--add-gray);
		font-size: 16px;
		line-height: math.div(26px,16px) * 100%;
		font-weight: 600;

		@media (max-width:$md4+px){
			 width: 100%; 
		}

	}
	// .popup-comment__label
	&__label {
		position: relative;
		cursor: pointer;
		max-width: 135px;
		span {
			padding-left: 28px;

			font-size: 13px;
			line-height: math.div(21px,13px) * 100%;
			font-weight: 600;
			&::after{
				content:'';
				position: absolute;
				top: 50%;
				left: 0;
				transform: translate(0px,-50%);
				width: 16px;
				height: 16px;
				border-radius: 50%;
				border: 1px solid var(--lightest-gray);
			}
			&::before{
				content:'';
				position: absolute;
				top: 5px;
    			left: 3px;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: var(--main-white);

				transition: background-color 0.3s ease 0s;
			}
		}
	}
	// .popup-comment__radio
	&__radio {
		position: absolute;
		width: 0;
		height: 0;
		opacity: 0;
		visibility: hidden;

		&:checked + span::before {
			background-color: var(--main-yellow);
		}
	}
	&__textarea {
		max-width: 480px;
		height: 104px;
		padding: 13px 16px;

		color: var(--add-gray);
		line-height: math.div(26px,16px) * 100%;
		resize: none;
		font-weight: 600;

		border: 1px solid var(--main-gray);

		@media (max-width:$md4+px){
			 width: 100%; 
		}
	}
	// .popup-comment__text
	&__text {
		max-width: 480px;
		margin-bottom: 26px;
		line-height: math.div(26px,16px) * 100%;

		@media (max-width:$md4+px){
			max-width: 288px;
			margin: 0px auto 24px;

			font-size: 10px; 
			text-align: center;
		}
	}
	// .popup-comment__btn
	&__btn {
		padding: 13px 26px;

		font-size: 16px;
		font-weight: 800;
		letter-spacing: math.div(.3px,16px) * 1em;
		line-height: math.div(26px,16px) * 100%;

		@media (max-width: 425px){
			  width: 100%;
		}
	}

	.form__error {
		margin-top: 4px;
		padding-left: 16px;

		font-size: 13px;
		line-height: 21px;

		color: var(--red);
	}
}


