@use 'sass:math';

$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1375;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;


.product-page {
    // .product-page__breadcrumbs
    &__breadcrumbs {
        margin-bottom: 20px;
    }
    // .product-page__hero
    &__hero {
        @include adaptiv-value("margin-bottom", 109, 34, 1);
    }
    // .product-page__picking
    &__picking {
        @include adaptiv-value("margin-bottom", 235, 50, 1);
    }
     // .page__specifications
     &__specifications {
         @include adaptiv-value("margin-bottom", 128, 48, 1);

         @media (max-width:$md4+px){
            display: none;
            &._active{
                display: block;
            } 
       }
    }

    &__product-reviews{
        @include adaptiv-value("margin-bottom", 128, 48, 1);

        @media (max-width:$md4+px){
            display: none;
            &._active{
                display: block;
            } 
       }
    }
    &__benefits{
        @include adaptiv-value("margin-bottom", 128, 48, 1);
        
        @media (max-width:$md4+px){
             display: none;
             &._active{
                 display: block;
             } 
        }
    }
    // .product-page__about-us
    &__about-us {
        margin-bottom: 110px;
        @media (max-width:$md4+px){
             margin-bottom: 48px; 
             display: none;
             &._active{
                 display: block;
             }
        }
    }
        // .product-page__mob-slider
        &__mob-slider {
        display: none;
        @media (max-width:$md4+px){
            display: block;
            margin-bottom: 26px;
        }
    }

    // .product-page__buy-more
    &__buy-more {
        @include adaptiv-value("margin-bottom", 128, 56, 1);
    }



     // .product-page__similar-goods
     &__similar-goods {
        @include adaptiv-value("margin-bottom", 196, 56, 1);
    }
}

@import "mixins";
@import "../product";
@import "hero-product-page";
@import "picking";
@import "specifications";
@import "product-reviews";
@import "mob-slider";



