@use 'sass:math';

.specifications {
    // .specifications__container
    &__container {
    }
    // .specifications__title
    &__title {
        margin-bottom: 64px;

        @media (max-width:$md4+px){
            display: none;
            margin-bottom: 34px; 
        }
    }
    // .specifications__content
    &__content {
    }
    // .specifications__item
    &__item {
        display: flex;

       border-top: 1px solid rgba(59, 59, 59, 0.1);

       @media (max-width:$md3+px){
            flex-direction: column; 
       }

        &:not(:last-child){
            margin:0px 0px 45px 0px;
            border-top: none;

            @media (max-width:$md3+px){
                margin:0px 0px 24px 0px;
            }
        }
    }
    // .specifications__label
    &__label {
        flex: 0 0 288px;
        padding-top: 8px;

        font-weight: 600;
        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md2+px){
            flex: 0 0 200px;
        }
        @media (max-width:$md3+px){
            flex: 0 1 auto; 
            width: 100%;
            text-align: center;
            padding-bottom: 15px;

             border-bottom: 1px solid var(--light-gray);
        }
    }
    // .specifications__body
    &__body {
        flex: 0 1 math.div(1056,1344) * 100%;

        @media (max-width:$md3+px){
            flex: 0 1 auto; 
       }
    }
    // .specifications__row
    &__row {

        display: flex;
        align-items:center;
        padding: 12px 32px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        @media (max-width:$md3+px){
             padding: 8px; 
        }

        &:nth-child(even) {
            background-color: rgba(59, 59, 59, 0.05);
        }
    }
     // .specifications__link
     &__link {
        width: 100%;
        margin-top: 10px;

        color: var(--main-yellow);
        font-size: 13px;
        font-weight: 800;
        line-height: 21px;
        text-align: center;
        
        background-color: transparent;

        transition: color 0.3s ease 0s;

        @media (any-hover: hover){
            &:hover{
                color: var(--hover-color);
            }
        }
     }
    // .specifications__name
    &__name {
        flex: 0 1 50%;
        color: rgba(17, 17, 17, 0.45);

        @media (max-width:$md4+px){
             margin-right: 20px; 
        }
    }
    // .specifications__value
    &__value {
        flex: 0 1 50%;

        @media (max-width:$md3+px){
            text-align: right; 
        }
    }
}

