@use 'sass:math';

//====================================================================================================
.footer {
    padding: 20px 0px 32px;
    background-color: var(--darkest-gray);

    @media (max-width: 620px){
        padding-bottom: 165px;
    }
    @media (max-width:$md4+px){
          padding-bottom: 128px;
    }

    &._active{
        @media (min-width: 620px){
            .footer__product-bar {
                position: relative;
                margin-bottom: 20px;
            }
        }
    }

    // .footer__content
    &__content {}

    // .footer__top
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 20px;

        border-bottom: 1px solid var(--main-gray);
    }

    // .footer__social
    &__social {
        flex: 0 0 165px;

        a {
            display: inline-flex;
            width: 40px;
            height: 40px;

            @media (max-width:$md4+px) {
                width: 24px;
                height: 24px;
            }

            &:not(:last-child) {
                margin: 0px 12px 0px 0px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    // .footer__menu
    &__menu {
        flex: 0 1 math.div(813, 1345) * 100%;

        @media (max-width:$md1+px){
            flex: 0 1 400px; 
        }
        @media (max-width:$md2+px){
            flex: 1 1 auto;
        }
    }

    // .footer__info
    &__info {
        flex: 0 1 math.div(382, 1345) * 100%;
        margin-left: 20px;
        padding-top: 10px;

        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
        font-family: var(--add-font);

        @media (max-width:620px){
            flex: 0 1 auto;
            margin: 0px 0px 28px 0px;
        }
    }

    // .footer__link-phone
    &__link-phone {
        margin-bottom: 8px;

        font-size: 24px;

        color: var(--main-white);
        white-space: nowrap;

        @media (max-width:$md4+px) {
            font-size: 19px;
            line-height: math.div(24px, 19px) * 100%;
            font-weight: 700;
            letter-spacing: math.div(.3px, 19px) * 1em;
        }
    }

    // .footer__shedule
    &__shedule {
        font-weight: 400;
        font-size: 14px;
        line-height: math.div(16px, 10px) * 100%;

        color: var(--medium-gray);

        @media (max-width:$md4+px) {
            color: var(--darken-white);
            font-weight: 500;
            font-size: 10px;
            line-height: 16px;
        }
    }

    // .footer__bottom
    &__bottom {}

    // .footer__user-bar
    &__user-bar {
        display: none;
        @media (max-width: 620px){
            max-width: 620px;
            position: fixed;
            bottom: 0;
            display: block;
        }
    }

    // .footer__product-bar
    &__product-bar {
        position: fixed;
        bottom: 0;
        z-index: 10;
        width: 100%;
        max-width: 1920px;

        @media (max-width: 620px){
            bottom: 53px;
        }
    }

}

.social-footer {
    margin-right: 20px;
    // .social-footer__vk
    &__vk {}

    // .social-footer__vfb
    &__vfb {}

    // .social-footer__insta
    &__insta {}
}

.menu-footer {


    // .menu-footer__list
    &__list {
        max-width: 587px;
        margin-left: auto;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        justify-items: center;
        grid-gap: 38px;

        @media (max-width:$md1+px){
            max-width: none;
            grid-template-columns: repeat(3,1fr);
        }
        @media (max-width:$md2+px){
            grid-template-columns: repeat(auto-fit,minmax(85px, 1fr));
            justify-items: center;
            margin: 0;
            grid-gap: 25px;
        }
        @media (max-width: 620px){
              display: flex;
              justify-content: space-evenly;
              align-items:center;
              flex-wrap: wrap;
              grid-gap: 0;
              margin: 0 -6px;
        }
    }

    // .menu-footer__link
    &__link {

        @media (max-width: 620px){
            margin-bottom: 12px;
            padding: 0 6px;

        }
        

        a {
            font-size: 13px;
            font-weight: 600;
            text-transform: uppercase;
            color: var(--medium-gray);
            white-space: nowrap;

            transition: color 0.3s ease 0s;

            @media (max-width:$md4+px) {
                color: var(--semi-gray);
                line-height: 21px;
            }

            @media (any-hover: hover) {
                &:hover {
                    color: var(--hover-color);
                }
            }
        }
    }
}

.bottom-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;

    font-family: var(--add-font);

    @media (max-width:$md2+px){
        flex-wrap: wrap;
    }
    @media (max-width: 620px){
          flex-direction: column;
          justify-content:center;
          align-items: center;
    }

    @media (max-width:$md4+px) {
        padding-top: 32px;

        font-family: var(--main-font);
    }

    // .bottom-footer__help-links
    &__help-links {
        flex: 0 1 math.div(592, 1345) * 100%;

        display: flex;
        align-items: center;

        @media (max-width:$md2+px){
            flex: 1 1 auto;
            justify-content: space-between;
            margin-bottom: 30px;
        }
        @media (max-width: 620px){
            flex: 0 1 auto;
            flex-direction: column;
            justify-content:center;
            align-items: center;

            margin-bottom: 48px;
        }
    }

    // .bottom-footer__help-link
    &__help-link {
        &:not(:last-child) {
            margin: 0px 24px 0px 0px;

            @media (max-width:620px) {
                margin: 0px 0px 8px 0px;
            }
        }

        a {
            white-space: nowrap;
            color: var(--medium-gray);
            font-size: 14px;

            transition: color 0.3s ease 0s;

            @media (any-hover: hover) {
                &:hover {
                    color: var(--hover-color);
                }
            }

            @media (max-width:$md4+px) {
                font-size: 13px;
                line-height: 21px;
                color: var(--darken-white);
            }
        }
    }

    // .bottom-footer__info
    &__info {
        flex: 0 1 math.div(383, 1345) * 100%;

        display: flex;
        align-items: center;

        color: var(--medium-gray);
        font-size: 14px;
        font-weight: 400;
        @media (max-width:$md2+px){
            flex: 0 1 auto;
        }
        @media (max-width: 620px){
              width: 100%;

              justify-content:space-between;
        }

        @media (max-width:$md4+px) {
            font-size: 13px;
            line-height: 21px;
            font-weight: 500;
            color: var(--darken-white);
        }
    }

    // .bottom-footer__email
    &__email {
        white-space: nowrap;
        margin-right: 24px;

        transition: color 0.3s ease 0s;

        @media (any-hover: hover) {
            &:hover {
                color: var(--hover-color);
            }
        }
    }

    // .bottom-footer__address
    &__address {
        white-space: nowrap;
        @media (max-width:$md2+px) {
            display: none;
        }
    }
}

.user-bar-footer {
    @media (max-width: 620px){
        width: 100%;
        background-color: var(--main-white);
        // .user-bar-footer__items
        &__items {
            display: flex;
            justify-content:space-between;
            align-items: center;
        }
        // .user-bar-footer__item
        &__item {
            flex: 0 1 20%;
    
            display:flex;
            flex-direction: column;
            justify-content:center;
            align-items: center;
    
            padding: 6px 13px 4px;

            color: var(--main-gray);

            &._active{
                svg {
                    --footer-active-color: var(--hover-color);
                }
                color: var(--hover-color);
            }
            // .user-bar-footer__item_main
            &_main {
            }
            // .user-bar-footer__item_catalog
            &_catalog {
            }
            // .user-bar-footer__item_favorites
            &_favorites {
            }
            // .user-bar-footer__item_compare
            &_compare {
            }
            // .user-bar-footer__item_cart
            &_cart {
            }
        }
        // .user-bar-footer__icon
        &__icon {
            margin-bottom: 5px;
        }
        // .user-bar-footer__label
        &__label {
    
            font-size: 10px;
            line-height: 12px;
            letter-spacing: -0.24px;
        }
    }
}

.product-bar {
    background-color: var(--darkest-gray);
    // .product-bar__content
    &__content{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    // .product-bar__column
    &__column {
        flex: 0 1 math.div(606,1344) * 100%;

        display: flex;
        flex-direction: column;

        @media (max-width:$md1+px){
             flex: 0 1 40%; 
        }
        @media (max-width:$md2+px){
            flex: 0 1 auto  ;
        }
    }
    // .product-bar__info
    &__info {
        display: flex;
        align-items:center;
        margin-bottom: 12px;

        @media (max-width:$md2+px){
            display: none;
        }
    }
    // .product-bar__name
    &__name {
        font-size: 22px;
        line-height: math.div(28px,22px) * 100%;
        letter-spacing: math.div(.3px,22px) * 1em;
        font-weight: 700;
        color: var(--main-white);
    }
    // .product-bar__available
    &__available {
        display: flex;
        align-items:center;
        margin-left: 8px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        font-weight: 600;
        color: var(--ligther-green);

        svg {
            margin-right: 4px;
        }
    }
    // .product-bar__row
    &__row {
        display: flex;
        align-items:center;

        @media (max-width: 620px){
              flex-direction: column;
              align-items: flex-start;
        }

        .color-scheme-product {
            border-color: var(--main-white);
            padding: 0;
            background-color: var(--main-white);
        }
    }
    // .product-bar__color
    &__color {
        display: flex;
        align-items:center;
    }
    // .product-bar__icon
    &__icon {
        margin-right: 12px;

        @media (max-width: 620px){
              margin: 0px 0px 4px 0px;
        }
    }
    // .product-bar__color-name
    &__color-name {
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        color: var(--lighter-gray);

        @media (max-width:$md1+px){
             display: none; 
        }
    }
    // .product-bar__diameter
    &__diameter {
        margin-left: 21px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        color: var(--lighter-gray);
        @media (max-width: 620px){
              margin-left: 0;
        }

        &::before{
            content:'/';
            margin-right: 12px;
            display: inline-block;
            @media (max-width: 620px){
                  display: none;
            }
        }
    }
    // .product-bar__articol
    &__articol {
        display: flex;
        align-items:center;
        margin-left: 22px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        font-weight: 600;
        color: var(--main-white);
        opacity: .75;

        @media (max-width:$md2+px){
            display: none;
        }

        &::before{
            content:'';
            width: 5px;
            height: 5px;
            border-radius: 50%;
            margin-right: 16px;
            background-color: var(--main-white);
            opacity: .75;
        }
    }
    // .product-bar__buttons
    &__buttons {
        flex: 0 0 132px;

        display: flex;
        align-items: center;

        @media (max-width:$md2+px){
            flex: 0 1 auto;
            margin-bottom: 15px;
        }
        @media (max-width: 620px){
              display: none;
        }
    }
    // .product-bar__button
    &__button {
        background-color: transparent;

        &:not(:last-child){
            margin:0px 32px 0px 0px;
        }

        // .product-bar__button_compare
        &_compare {
        }
        // .product-bar__button_share
        &_share {
        }
        // .product-bar__button_favorites
        &_favorites {
            ._favourites {
                svg path {
                    fill: var(--main-white);
                }
            }
        }
    }
    // .product-bar__main-column
    &__main-column {
        flex: 0 1 math.div(606,1344) * 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        @media (max-width:$md2+px){
            flex: 1 1 auto;
        }
        @media (max-width:$md3+px){
             margin-right: -15px; 
        }
    }
    // .product-bar__prices
    &__prices {
        margin-right: 32px;
        font-weight: 700;

        @media (max-width:$md4+px){
            margin-right: 16px;
            font-weight: 600;  
        }
    }
    // .product-bar__new-price
    &__new-price {
        font-size: 28px;
        color: var(--main-white);
        line-height: math.div(36px,28px) * 100%;
        letter-spacing: math.div(.3px,28px) * 1em;

        @media (max-width:620px){
             font-size: 16px;
        }

        span {
            margin-left: 8px;
            font-size: 22px;
            opacity: .75;

            @media (max-width:$md4+px){
                margin-left: 0px;
                 font-size: 16px; 
            }
        }
    }
    // .product-bar__old-price
    &__old-price {
        font-size: 19px;
        color: var(--medium-gray);
        text-decoration: line-through;
        text-align: right;

        @media (max-width:620px){
             font-size: 10px; 
        }

        span {
            margin-left: 4px;
            font-size: 16px;
            font-weight: 600;

            @media (max-width:$md4+px){
                 margin-left: 0;
                 font-size: 10px; 
            }
        }
    }
    // .product-bar__cart
    &__cart {
        flex: 0 1 math.div(320,606) * 100%;

        @media (max-width:$md4+px){
             flex: 0 1 40%; 
        }
    }
    // .product-bar__btn
    &__btn {
        padding: 37px 16px;

        @media (max-width:$md4+px){
             padding: 24px 16px; 
        }
        span {
            font-size: 16px;

            @media (max-width:$md4+px){
                 font-size: 13px; 
            }
        }
        ._btn__text_mob {
            display: none;
            @media (max-width:$md4+px){
                 display: inline; 
                 margin-left: 12px;
            }
        }
        &_add-cart {

        }
        &_go-cart {
            display: none;
            &._active{
                display: flex;
                background-color: var(--dark-gray);
                border-color: var(--hover-color);
                color: var(--hover-color);

                @media (any-hover: hover){
                    &:hover{
                        background-color: var(--darken-gray);
                    }
                }
            }
        }

    }
}




