@use 'sass:math';

.sidebar {
   display: none;
    height: 705px;
    max-height: 85vh;

    overflow: auto;

    background-color: var(--main-white);

    &._lock,
    &._active{
        display: flex;
    }

    // .sidebar__nav
    &__nav {
        flex: 0 1 math.div(284,1375) * 100%;
        padding-top: 29px;

        background-color: var(--light-gray);
    }
    // .sidebar__list
    &__list {
    }
    // .sidebar__item
    &__item {
        position: relative;
        padding-left: 20px;

        color: var(--add-gray);
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        font-weight: 600;

        transition: color 0.3s ease 0s;
        cursor: pointer;

        @media (any-hover: hover){
            &:hover{
                color: var(--hover-color);
            }
        }

        &::after{
            content:'';
            position: absolute;
            top: -5px;
            left: 0;
            width: 0px;
            height: 0;

            background-color: var(--main-yellow);
            border-radius: 4px;

            transition: height 0.3s ease 0s,
                         width 0.3s ease 0s;
        }

        &:not(:last-child){
            margin:0px 0px 17px 0px;
        }

        &._active{
            color: var(--main-black);

            &::after{
                height: 30px;
                width: 4px;
            }
        }
    }
    // .sidebar__content
    &__content {
        display: none;
        flex-direction: column;
        flex-wrap: wrap;
        padding: 29px 0px 0px 96px;

        background-color: var(--main-white);

        &._active{
            display: flex;
        }

    }
    // .sidebar__column
    &__column {
        margin-right: 64px;
        &:not(:last-child){
            margin-bottom: 32px;
        }
    }
    // .sidebar__label
    &__label {
        margin-bottom: 12px;

        font-weight: 600;
        font-size: 16px;
        line-height: math.div(26px,16px) * 100%;
    }
    // .sidebar__group-list
    &__group-list {
        
    }
    // .sidebar__group-item
    &__group-item {
        max-width: 127px;
        &:not(:last-child){
            margin:0px 0px 8px 0px;
        }

        a {
            color: var(--dark-gray);
            font-size: 13px;
            line-height: math.div(21px,13px) * 100%;
            text-align: center;

            transition: color 0.3s ease 0s;

            @media (any-hover: hover){
                &:hover{
                    color: var(--hover-color);
                }
            }
        }
    }
}
