//<,  >===============================================================================================
@import "null";

body {
	color: var(--main-font-color);
	font-size: 16px;
	font-weight: 500;
	&._lock {
		overflow: hidden;
	}

	@media (max-width:$md4+px){
		 font-size: 13px; 
	}
}
//</,  >===============================================================================================

//<>===========================================================================================================
.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	&._loaded {
	}
}
//</>===========================================================================================================

//< >===========================================================================================================
._container {
	max-width: $maxWidthContainer + px;
	margin: 0 auto;
	padding: 0 15px;
	@media (max-width: $md1+px) {
		max-width: 970px;
	}
	@media (max-width: $md2+px) {
		max-width: 750px;
	}
	@media (max-width: $md3+px) {
		max-width: none;
		padding: 0 15px;
	}
}
//</ >===========================================================================================================

