@use 'sass:math';
._title {
	margin: 0px 0px math.div(33px,34px) * 1em 0px; 

	@include adaptiv-font(34,19,1);
	letter-spacing: math.div(.3px,34px) * 1em;
	line-height: math.div(40px,34px) * 100%;
	font-weight: 700;

	text-align: center;
}
._arrow {
	width: 48px;
	height: 48px;
	display:flex;
	justify-content:center;
	align-items: center;

	border: 1px solid #e7e7e7;
	border-radius: 50%;
	cursor: pointer;

	box-shadow: 0 6px 10px rgba(60, 61, 105, 0.1);
	background-color: var(--main-white);

	transition: background-color 0.3s ease 0s,
				border-color .3s ease 0s;


	&-right {
		svg{
			transform: rotate(-180deg);
		}
	}

	@media (any-hover: hover){
		&:hover{
			background-color: #f2f2f2;
			border: 1px solid #f2f2f2;

			svg {
				--arrow-hover-color: #111;
			}
		}
	}
}
._favourites {
	display: inline-flex;
	position: relative;
	width: 20px;
	height: 18px;
	cursor: pointer;

	svg {
		position: absolute;
		top: 0;
		left: 0;
		width: 20px;
		height: 20px;

		transition: opacity 0.3s ease 0s;
	}

	@media (any-hover: hover){
		&:hover{
			svg path {
				fill: var(--dark-gray);
			}
		}
	}

	&-active {
		opacity: 0;
	}

	&._active{
		._favourites-active{
			opacity: 1;
			path {
				fill: var(--main-yellow);
			}
		}
		._favourites-inactive{
			opacity: 0;
		}
		@media (any-hover: hover){
			&:hover{
				svg path {
					fill: var(--hover-color);
				}
			}
		}
	}

	svg path {
		transition: fill 0.3s ease 0s;
	}
}
._show-more-link {
	color: var(--main-yellow);
	font-weight: 800;
	letter-spacing: math.div(.3px, 16px) * 1em;
	line-height: math.div(26px, 16px) * 100%;

	transition: color 0.3s ease 0s;

	@media (any-hover: hover) {
		& + span {
			transition: transform 0.3s ease 0s;
		}
		&:hover {
			color: var(--hover-color);

			& + span {
				transform: translate(3px,0px);
			}
		}
	}
	
}
._not-svg {

	img[src$=".png"],
	img[src$=".webp"],
	img[src$=".jpg"] {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
._hidden {
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
}
._hide {
	display: none;
} 


