@use 'sass:math';


.bank {
    // .bank__title
    &__title {
        margin-bottom: 56px;
        @media (max-width:$md4+px){
             margin-bottom: 24px; 
        }
    }
    // .bank__credit
    &__credit {
        margin-bottom: 56px;
        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }
    }
    // .bank__installment
    &__installment {
        margin-bottom: 56px;
        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }
    }
    // .bank__town-search
    &__town-search {
    }
}
.credit {
    
    // .credit__container
    &__container {
    }
    // .credit__title
    &__title {
        margin-bottom: 24px;

        font-size: 28px;
        font-weight: 700;
        line-height: math.div(36px,28px) * 100%;
        letter-spacing: math.div(.3px,28px) * 1em;

        @media (max-width:$md4+px){
             margin-bottom: 16px; 

             font-size: 16px;
        }
    }
    // .credit__content
    &__content {
        max-width: 672px;
    }
    // .credit__text
    &__text {
        margin-bottom: 40px;

        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md4+px){
             margin-bottom: 32px; 
        }

        p {
            &:not(:last-child){
                margin-bottom: 16px;

                @media (max-width:$md4+px){
                    margin-bottom: 8px; 
                }
            }
        }
    }
    // .credit__partners
    &__partners {
        max-width: 672px;
    }
}
.partners {
    
    // .partners__text
    &__text {
        margin-bottom: 32px;

        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md4+px){
             margin-bottom: 24px; 
        }
    }
    // .partners__logos
    &__logos {
        display: grid;
        grid-template-columns: repeat(4, 100px);
        grid-gap: 35px;
        justify-content: space-between;

        @media (max-width:600px){
            grid-template-columns: repeat(3, 100px);
        }
        @media (max-width: 425px){
            grid-template-columns: repeat(2, 100px);
            justify-content: space-evenly;
            grid-gap: 24px;
        }
    }
    // .partners__logo
    &__logo {
        display: flex;
        justify-content:center;
        align-items: center;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
}
.installment {
    
    // .installment__container
    &__container {
    }
    // .installment__title
    &__title {
    }
    // .installment__content
    &__content {
        max-width: 672px;

        p {
            line-height: math.div(26px,16px) * 100%;

            @media (max-width:$md4+px){
                 font-size: 13px; 
            }
        }
    }
}
.town-search {
    margin-bottom: 96px;

    @media (max-width:$md4+px){
         margin-bottom: 80px; 
    }
   
    // .town-search__container
    &__container {
    }
    // .town-search__title
    &__title {
    }
    // .town-search__content
    &__content {
        max-width: 672px;
    }
    // .town-search__text
    &__text {
        margin-bottom: 16px;

        line-height: math.div(26px,16px) * 100%;

        @media (max-width:$md4+px){
             margin-bottom: 32px;
        }
    }
    // .town-search__form
    &__form {
        position: relative;
    }
    // .town-search__input
    &__input {
        width: 100%;
        padding: 13px 44px;

        line-height: math.div(26px,16px) * 100%;
        font-weight: 600;

        border: 2px solid var(--dark-gray);
    }
    // .town-search__icon
    &__icon {
        position: absolute;
        top: 13px;

        &_search {
            left: 16px;
        }
        &_close {
            top: 18px;
            right: 16px;
            cursor: pointer;
        }
    }
    // .town-search__info
    &__info {
        margin: 16px 0px 0px 0px;
        display: none;

        @media (max-width:$md4+px){
             margin-top: 8px; 
        }

        &._show {
            display: block;
        }
    }
    // .town-search__add-text
    &__add-text {
        display: none;
        margin-bottom: 16px;

        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        color: var(--add-gray);


    }
    // .town-search__list
    &__list {
        font-size: 19px;
        line-height: math.div(32px,19px) * 100%;
        letter-spacing: math.div(0.3px,19px) * 1em;

        @media (max-width:$md4+px){
             font-size: 16px; 
        }

        li {
            &:not(:last-child){
                margin:0px 32px 0px 0px;

                @media (max-width:$md4+px){
                     margin-bottom: 16px; 
                }
            }
        }
    }
}
