@use 'sass:math';

//<FORM ()>====================================================================================================
.form {
	&__row {}

	&__column {}

	&__line {}

	&__input {}

	&__button {}

	&__error {}
}

//</FORM>====================================================================================================

//<BUTTONS (a.btn+tab)>====================================================================================================
.btn {
	display: inline-flex;
	padding: 0px 0px;

	@media (min-width: 992px) {
		&:hover {}
	}

	&._fw {
		width: 100%;
	}
}

._btn {
	white-space: nowrap;

	display: inline-flex;
	justify-content: center;
	align-items: center;

	background-color: var(--main-yellow);
	border: 2px solid transparent;

	letter-spacing: math.div(.3px, 16px) * 1em;

	color: var(--main-white);

	transition: border-color 0.3s ease 0s,
		background-color 0.3s ease 0s;

	@media (any-hover: hover) {
		&:hover {
			background-color: var(--hover-color);
		}
	}

	&:focus {
		border-color: var(--black-color);
		background-color: var(--hover-color);
	}

	&-add-cart {
		position: relative;
		width: 100%;
		padding: 13px 15px;

		font-weight: 800;
		line-height: 100%;

		@media (max-width:$md3+px) {
			height: 36px;
		}
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 14px;

		@media (max-width:$md3+px) {
			margin-right: 8px;
		}

		@media (max-width:$md4+px) {
			margin-right: 0;
		}
	}

	&__text {
		@media (max-width:$md4+px) {
			display: none;
		}
	}
}

//</BUTTONS>====================================================================================================

//<SELECT (sel+tab)>====================================================================================================
.select {
	position: relative;
	-webkit-tap-highlight-color: rgba(0,0,0,0);

	// .select__item
	&__item {
		position: relative;

		color: rgba(17, 17, 17, 0.55);
		font-weight: 600;

		@media (max-width:$md4+px){
			font-size: 13px; 
	   }
	}

	// .select__title
	&__title {
		background-color: #fff;
		cursor: pointer;
		border-radius: 4px;
	}

	// .select__value
	&__value {
		display: flex;
		height: 26px;
		padding: 0px 5px;
		align-items: center;
		justify-content: space-between;
		flex-direction: row-reverse;

		span {
			height: 1em;
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;

			color: var(--add-gray);

			transition: color 0.3s ease 0s;

			@media (any-hover: hover){
				&:hover{
					color: var(--main-black);
				}
			}
		}

		&:before {
			content: "";
			display: inline-block;
			margin-left: 10px;
			transition: all 0.3s ease 0s;
			width: 15px;
			height: 12px;
			background: url("../img/icons/select.svg") center / 100% no-repeat;

			@media (max-width:$md4+px){
				 margin-left: 6px;
				 width: 10px;
				 height: 11px; 
			}
		}
	}

	&__input {
		width: 100%;
		background-color: transparent;
		height: 100%;
	}

	// .select__options
	&__options {
		display: none;
		position: absolute;
		top: 20px;
		left: -5px;

		width: 185px;
		padding: 10px 0px 5px 0px;

		background-color: #fff;
		border-radius: 0 0 4px 4px;

		@media (max-width:$md4+px){
			 padding: 5px 0; 
		}
	}

	// .select__option
	&__option {
		cursor: pointer;
		padding: 5px 15px;
		margin: 0px 0px 0px 0px;

		@media (min-width: 992px) {
			&:hover {
				background: #d9d9d9;
			}
		}
	}

	&._active {
		z-index: 5;

		.select__value {
			&:before {
				transform: rotate(-180deg);
			}
		}

		.select__options {
			display: block;
		}
	}
}

//</SELECT>====================================================================================================

//<INPUT (inp+tab)>====================================================================================================
input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

.input {
	border-radius: 0 !important;
	width: 100%;
	display: block;
	padding: 0px 20px;

	&._focus {}

	&._error {}
}

textarea.input {
	resize: none;
	padding: 0px 0px;
}

//</INPUT>====================================================================================================

//<CHECK (chk+tab)>====================================================================================================
.checkbox {
	position: relative;

	// .checkbox__input
	&__input {
		position: absolute;
		width: 0;
		height: 0;
		opacity: 0;
		visibility: hidden;

		&:checked+.checkbox__text:before {
			background: #fff url("../img/icons/sprites/sprite.svg#chekmark") center no-repeat;
		}
	}

	// .checkbox__text
	&__text {
		display: inline-flex;
		align-items: center;
		position: relative;
		font-size: 16px;
		color: #ffffff;
		letter-spacing: -0.3px;
		line-height: calc(20 / 16);
		cursor: pointer;

		&:before {
			content: url("../img/icons/sprites/sprite.svg#chekmark");
			align-self: flex-start;
			margin: 0px 14px 0px 0px;
			flex: 0 0 20px;
			left: 0;
			top: 0;
			width: 20px;
			height: 18px;
			background: #fff;
			border: 1px solid #a7a9ac;
		}
	}

	a {
		color: #fff;
		text-decoration: underline;

		@media (min-width: 992px) {
			&:hover {
				text-decoration: none;
			}
		}
	}

	&._error {
		.checkbox__text {
			&:before {
				border: 1px solid #ee1212;
			}
		}
	}
}

//</CHECK>====================================================================================================

//<OPTIONS (opt+tab)>====================================================================================================
.options {
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	// .options__item
	&__item {
		position: relative;
		cursor: pointer;

		&._error {}
	}

	// .options__input
	&__input {
		position: absolute;
		width: 0;
		height: 0;
		opacity: 0;
		left: 0;
		top: 0;
		visibility: hidden;

		&:checked+.options__text:before {}

		&:checked+.options__text:after {
			transform: scale(1);
		}
	}

	// .options__text
	&__text {
		display: inline-flex;
		align-items: center;
		position: relative;
		font-size: 16px;
		color: #ffffff;
		letter-spacing: -0.3px;
		line-height: calc(20 / 16);

		&:before {
			content: "";
			align-self: flex-start;
			margin: 0px 14px 0px 0px;
			flex: 0 0 20px;
			left: 0;
			top: 0;
			border-radius: 50%;
			width: 20px;
			height: 18px;
			background: #fff;
			border: 1px solid #a7a9ac;
		}

		&:after {
			content: "";
			transition: all 0.3s ease 0s;
			transform: scale(0);
			width: 10px;
			height: 10px;
			border-radius: 50%;
			background-color: #77243a;
			position: absolute;
			left: 5px;
			top: 4px;
		}
	}
}

//</OPTIONS>====================================================================================================

//<RATING>====================================================================================================
.rating {
	display: flex;
	align-items: center;
	line-height: 0.75;
	transition: opacity 0.3s ease 0s;
}

.rating.rating_sending {
	opacity: 0.2;
}

.rating.rating_set .rating__active,
.rating.rating_set .rating__item {
	cursor: pointer;
}

.rating__body {
	position: relative;
	margin-right: 6px;

	&::before {
		content: "";
		display: block;
		color: var(--main-yellow);
	}
	&_active {
		&::before{
			content: "";
			color: var(--lightest-gray);
			font-size: 29px;
		}
	}

	&_product-item {
		&::before{
			@media (max-width:$md4+px) {
				content: "";
			}
		}
	}
}

.rating__active {
	position: absolute;
	width: 0%;
	height: 100%;
	top: 0;
	left: 0;
	overflow: hidden;

	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		color: var(--main-yellow);
	}
	&_active {
		font-size: 29px;
	}

	&_product-item {
		&::before{
			@media (max-width:$md4+px) {
				content: "";
			}
		}
	}
}

.rating__items {
	display: flex;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

.rating__item {
	flex: 0 0 20%;
	height: 100%;
	opacity: 0;
}

.rating__value {
	margin-right: 14px;

	color: var(--main-yellow);
	font-size: 13px;
	line-height: math.div(21px, 13px) * 100%;

	@media (max-width:$md4+px) {
		font-size: 10px;
		font-weight: 600;
	}

	&_popup {
		position: absolute;
		clip: rect(0 0 0 0);
		width: 1px;
		height: 1px;
		margin: -1px;
	}
}

//</RATING>====================================================================================================

//<QUANTITY>====================================================================================================
.quantity {
	width: 88px;
	height: 40px;
	display: flex;
	border: 1px solid rgba(166, 166, 166, 0.45);

	// .quantity__button
	&__button {
		flex: 0 0 30px;
		position: relative;
		cursor: pointer;

		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			transition: all 0.3s ease 0s;
			margin: 0px 0px 0px -4.5px;
			background-color: #a6a6a6;
			width: 9px;
			height: 1px;
		}

		@media (min-width: $md2+px) {
			&:hover {

				&::before,
				&::after {
					background-color: rgba(0, 0, 0, 0.5);
				}
			}
		}

		// .quantity__button_plus
		&_plus {
			&::before {
				transform: rotate(-90deg);
			}
		}

		// .quantity__button_minus
		&_minus {}
	}

	// .quantity__input
	&__input {
		flex: 1 1 auto;

		input {
			height: 100%;
			color: #a6a6a6;
			font-size: 12px;
			width: 100%;
			text-align: center;
		}
	}
}

//</QUANTITY>====================================================================================================

