@use 'sass:math';


.picking {
    // .picking__container
    &__container {
    }
    // .picking__title
    &__title {
        @include adaptiv-value("margin-bottom", 120, 24, 1);
    }
    // .picking__content
    &__content {
        display: flex;
        justify-content:space-between;
    }
    // .picking__image
    &__image {
        flex: 0 2 math.div(864,1345) * 100%;
        margin-right: 30px;
        padding: 0px 0px 38.1% 0px;

        @media (max-width:$md1+px){
            display: none;
        }
    }
    // .picking__description
    &__description {
        flex: 0 0 324px;
        display:flex;
        flex-direction: column;

        -webkit-tap-highlight-color: rgba(0,0,0,0);

        @media (max-width:$md1+px){
             flex: 1 1 100%; 
        }
    }
    &__label {
        display: flex;
        align-items:center;
        margin: 0px 0px math.div(20px,19px) * 1em 0px; 

        font-size: 19px;
        font-weight: 700;
        line-height: math.div(24px,19px) * 100%;
        letter-spacing: math.div(.3px,19px) * 1em;
        

        @media (max-width:$md1+px){
             align-self: center; 
        }

        @media (max-width:$md4+px){
             font-size: 16px;
             font-weight: 600;
             line-height: math.div(24px,16px) * 100%;
        }
        svg {
            margin-left: 10px;
        }
    }
    // .picking__size
    &__size {
        margin-bottom: 27px;

        @media (max-width:$md4+px){
             margin-bottom: 16px; 
        }
    }

    // .picking_colors
    &__colors{
        @media (max-width:$md1+px){
            align-self: center; 
       }
        @media (max-width:$md4+px){
             display: block; 
        }
        .color-scheme-product {
            border-width: 2px;
            border-radius: 6px;
        }
    }


}
.size-picking {
    display: flex;
    flex-wrap: wrap;
    // .size-picking__item
    &__item {
        flex: 0 1 33.333%;

        display: flex;
        flex-direction: column;
        justify-content:center;
        align-items: center;

        margin-bottom: 4px;
        padding: 16px;

        border: 2px solid transparent;
        border-radius: 4px;
        cursor: pointer;

        transition: border-color 0.3s ease 0s,
                    background-color 0.3s ease 0s;

        @media (max-width:$md4+px){
             padding: 8px; 
             margin-bottom: 8px;
        }

        &._active{

            border-color: var(--main-yellow);
            background-color: #f8f8fd;
        }
    }
    // .size-picking__label
    &__label {
        margin: 0px 0px math.div(17px,16px) * 1em 0px; 

        font-size: 16px;
        font-weight: 600;

        @media (max-width:$md4+px){
             font-size: 13px; 
        }

    }
    // .size-picking__sublabel
    &__sublabel {
        font-size: 13px;
        color: rgba(17, 17, 17, 0.45);

        @media (max-width:$md4+px){
             font-size: 10px; 
        }
    }
}
