@use 'sass:math';

.breadcrumbs {
    display: flex;
    align-items: center;

    font-size: 13px;
    line-height: 100%;
    line-height: math.div(21px,13px) * 100%;

    @media (max-width:$md3+px) {
        font-size: 12px;
        letter-spacing: 0.6px;
    }

    // .breadcrumbs__links
    &__links {
        @media (max-width: 350px){
            font-size: 10px;
        }

        // .breadcrumbs__links_prev
        &_prev {
            position: relative;
            margin-right: 23px;


            color: rgba(63, 63, 63, 0.75);

            @media (min-width: $md2+px) {
                &:hover {
                    &::before {
                        width: 100%;
                    }
                }
            }

            // @media (max-width:$md3+px) {
            //     margin-right: 19px;
            // }

            &::before {
                content: '';
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 0%;
                height: 1px;
                background-color: rgba(63, 63, 63, 0.75);

                transition: width 0.3s ease 0s;
            }

            &::after {
                content: '/';
                position: absolute;
                top: -1px;
                right: -16px;
                display: block;

                color: rgba(46, 54, 70, 0.25);
            }
        }

        // .breadcrumbs__links_current
        &_current {
            @media (max-width:$md4+px){
                 span {
                     display: none;
                 } 
            }
        }
    }
    &__folder {
        @media (max-width:$md4+px){
             display: none; 
        }
    }
    &__dots {
        display: none;
        @media (max-width:$md4+px){
            color: var(--color-gray);
             display: inline; 
        }
    }
}